export default {
    data() {
        return {
            filNum: '',
            address: ''
        };
    },
    onLoad(option) {
        this.filNum = option.fil_num;
        this.address = option.wallet_value;
    },
    methods: {
        save() {
            uni.navigateBack({
                delta: 2
            });
        }
    },
    onBackPress(options) {
        if (options.from === 'backbutton') {
            uni.navigateBack({
                delta: 2
            });
        }
    }
};
